import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TablePagination from "@material-ui/core/TablePagination";
import TableRow from "@material-ui/core/TableRow";
import { Container } from "react-bootstrap";

const columns = [
  { id: "nummer", label: "Nummer", minWidth: 170 },
  { id: "rubrik", label: "Rubrik", minWidth: 100 },
  {
    id: "ärendet",
    label: "Ärendet gäller",
    minWidth: 170,
    align: "right",
    format: value => value.toLocaleString()
  },
  {
    id: "uppdatering",
    label: "Senaste uppdatering",
    minWidth: 170,
    align: "right",
    format: value => value.toLocaleString()
  },
  {
    id: "status",
    label: "Status",
    minWidth: 170,
    align: "right",
    format: value => value.toFixed(2)
  }
];

function createData(nummer, rubrik, ärendet, uppdatering, status) {
  return { nummer, ärendet, uppdatering, status, rubrik };
}

